@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");

* {
  box-sizing: border-box;
  font-family: "Press Start 2P", "Arial", sans-serif;
}

:root {
  --bg-color: #000000;

  /* Border Colors are all transparent colors. These will tint or shade the background color of the square. */
  --border-left-color: rgba(0, 0, 0, 0.5);
  --border-top-color: rgba(0, 0, 0, 0.7);
  --border-right-color: rgba(170, 170, 170, 0.2);
  --border-bottom-color: rgba(92, 91, 91, 0.7);

  /* Square Colors: background colors for the squares. */
  --color-0: #000000; /* Black for empty squares to match the background */
  --color-1: #ff0000; /* Bright red */
  --color-2: #ffd500; /* Bright yellow */
  --color-3: #0000ff; /* Deep blue */
  --color-4: #800080; /* Purple */
  --color-5: #00ff00; /* Bright green */
  --color-6: #00ffff; /* Cyan */
  --color-7: #ff7f00; /* Orange */

  /* Button Colors */
  --button-color-t: rgba(50, 50, 50, 0.9); /* Dark Grey Top */
  --button-color-r: rgba(80, 80, 80, 0.9); /* Medium Grey Right */
  --button-color-l: rgba(120, 120, 120, 0.9); /* Light Grey Left */
  --button-color-b: rgba(0, 132, 255, 0.9); /* Bright Orange Bottom */

  --cols: 10;
  --rows: 18;
  --tile-size: 35px;
  --border-width: 2px;
}

body {
  background-image: url("./images/bg.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
}

/* App */
.App {
  --col-side: calc(var(--tile-size) * 4);
  --col-center: calc(var(--tile-size) * 10);
  --grid-height: calc(var(--tile-size) * 18);

  width: calc(var(--tile-size) * (4 + 1 + 10 + 1 + 4));
  margin: auto;
  display: grid;

  grid-gap: var(--tile-size) var(--tile-size);
  /* Defines three columns of `--col-side`, `--col-center`, and `--col-side` widths. */
  grid-template-columns: var(--col-side) var(--col-center) var(--col-side);
  /* Defines three rows of `100px`, `--grid-height`, and 100px. */
  grid-template-rows: 100px var(--grid-height) 100px;

  /* This defines the areas on the grid with names: 'h', 'l', 'c', 'r',
  and 'b'. The '.' represents an empty cell on the grid. */
  grid-template-areas:
    "h h h"
    "l c r"
    ". b .";
}

.grid-board {
  grid-area: c;
  display: grid;
  grid-template-columns: repeat(var(--cols), var(--tile-size));
  grid-gap: 0;
  align-self: flex-start;
}

.App-header {
  grid-area: h;
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(1px + 2vmin);
  color: white;
}

/* Next Block */
.next-block {
  grid-area: l;
  display: grid;
  grid-template-columns: repeat(4, var(--tile-size));
  align-self: flex-start;
}

/* Score Board */
.score-board {
  color: #fff;
  grid-area: r;
}

.score-board div {
  margin: 0.5em;
}

.score-board div:nth-child(2) {
  padding-bottom: 20px;
}

.score-board-button {
  width: 120px;
  display: block;
  padding: 1em;
  margin: 0.5em;
  border-width: 2px;
  background-color: #000;
  color: #fff;
  font-weight: bold;
  font-size: 0.8em;
  border-radius: 2px;
  border-top-color: var(--button-color-t);
  border-left-color: var(--button-color-l);
  border-right-color: var(--button-color-r);
  border-bottom-color: var(--button-color-b);
}

/* controls */
.controls {
  grid-area: b;
  display: flex;
  flex-direction: row;
}

/* Control Button */
.control-button {
  font-size: 0.2em;
  margin: 0.5em;
  --size: calc(var(--tile-size) * 2.5);
  width: var(--size);
  height: var(--size);
  text-align: center;
  display: block;
  border-width: 5px;
  border-top-color: var(--button-color-t);
  border-left-color: var(--button-color-l);
  border-right-color: var(--button-color-r);
  border-bottom-color: var(--button-color-b);
  background-color: #000;
  color: #fff;
  font-weight: bold;
  border-radius: 2px;
}

/* Message Popup Styles */
.message-popup {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: calc(var(--tile-size) * 100);
  height: calc(var(--tile-size) * 5);
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  animation: blink 1.5s linear infinite;
}

.message-popup.hidden {
  display: none;
}

@keyframes blink {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

/* Colors */
.color-0 {
  background-color: var(--color-0);
}

.color-1 {
  background-color: var(--color-1);
}

.color-2 {
  background-color: var(--color-2);
}

.color-3 {
  background-color: var(--color-3);
}

.color-4 {
  background-color: var(--color-4);
}

.color-5 {
  background-color: var(--color-5);
}

.color-6 {
  background-color: var(--color-6);
}

.color-7 {
  background-color: var(--color-7);
}

/* Grid Square */
.grid-square {
  border-style: solid;
  width: var(--tile-size);
  height: var(--tile-size);
  border-width: var(--border-width);
  border-left-color: var(--border-left-color);
  border-top-color: var(--border-top-color);
  border-right-color: var(--border-right-color);
  border-bottom-color: var(--border-bottom-color);
}
